import type { Ref } from 'preact/hooks';

export function getValue(
  pageX: number,
  max: number,
  step: number,
  dimensionsRef: Ref<{ width: number; x: number }>,
  isRtl: boolean
): number {
  const dimensions = dimensionsRef.current;
  if (!dimensions || dimensions.width === 0) {
    return -1;
  }
  let val = (max * (pageX - dimensions.x)) / dimensions.width;
  const stepNum = val / step;
  if (stepNum < 0.5) {
    val = 0;
  } else {
    val = Math.ceil(stepNum) * step;
  }
  return isRtl ? max - val : val;
}

export function getDimensions(element: HTMLElement | null) {
  if (!element) {
    return {
      width: 0,
      x: 0
    };
  }

  const rect = element.getBoundingClientRect();
  return {
    width: Math.round(rect.width),
    x: Math.round(rect.x + window.scrollX)
  };
}
