import { useRef } from 'preact/hooks';
import type { Ref } from 'preact/hooks';
import { useUser } from '../hooks/UNSAFE_useUser';
import { getValue } from './RatingUtils';

export type CommitDetail = {
  value: number;
};

export type InputDetail = {
  value?: number;
};

export const useEvents = (
  max: number,
  step: number,
  value: number,
  dimensionsRef: Ref<{ width: number; x: number }>,
  isInteractive: boolean,
  onCommit?: (detail: CommitDetail) => void,
  onInput?: (detail: InputDetail) => void
) => {
  const currentInput = useRef<number>();
  const { direction } = useUser();

  if (!isInteractive) {
    return {};
  }

  const isRtl = direction === 'rtl';

  const pointerUpHandler = (event: PointerEvent) => {
    const val = getValue(event.pageX, max, step, dimensionsRef, isRtl);
    if (val !== -1) {
      onCommit?.({ value: val } as CommitDetail);
    }
  };

  const hoverHandler = (event: PointerEvent) => {
    const val = getValue(event.pageX, max, step, dimensionsRef, isRtl);
    if (val !== -1 && val != currentInput.current) {
      onInput?.({ value: val } as InputDetail);
      currentInput.current = val;
    }
  };

  const blurHandler = () => {
    onCommit?.({ value: value } as CommitDetail);
  };

  const pointerLeaveHandler = () => {
    onInput?.({ value: undefined } as InputDetail);
    currentInput.current = undefined;
  };

  const keyboardHandler = (event: KeyboardEvent) => {
    const key = event.key;

    if (key === 'Tab') {
      return;
    }

    if (key === 'Enter') {
      onCommit?.({ value: value } as CommitDetail);
      return;
    }

    let inputValue = undefined;
    switch (key) {
      case 'ArrowLeft': {
        inputValue = Math.max(0, value - step);
        break;
      }
      case 'ArrowRight': {
        inputValue = Math.min(max, value + step);
        break;
      }
      case 'Home': {
        inputValue = 0;
        break;
      }
      case 'End': {
        inputValue = max;
        break;
      }
    }

    if (inputValue !== undefined) {
      onInput?.({ value: inputValue } as InputDetail);
      currentInput.current = inputValue;
    }

    event.preventDefault();
    event.stopPropagation();
  };

  return {
    onPointerUp: pointerUpHandler,
    onBlur: blurHandler,
    onPointerMove: hoverHandler,
    onKeyUp: keyboardHandler,
    onPointerLeave: pointerLeaveHandler
  };
};
